<template>
  <div id="section-content">
    <head-tabar></head-tabar>
    <div style="margin-top: 100px;position: absolute;border: 2px solid #b3b3b3;width: 900px;margin-left: 300px">
      <h2 style="color: #7b9e31;text-align: center">{{courseName}}</h2>
      <p style="text-align: center;">{{label}}</p>
      <div v-html="sectionContent" style="margin-left: 50px;margin-right: 50px;border-top:1px solid #b3b3b3;"></div>
    </div>
  </div>
</template>

<script>
import HeadTabar from "../../components/HomeTaber/HeadTabar";
export default {
  name: "SectionContent",
  components:{
    HeadTabar
  },
  data(){
    return{
      sectionContent:localStorage.getItem('sectionContent'),
      label:localStorage.getItem('label'),
      courseName:localStorage.getItem('courseName')
    }
  }
}
</script>

<style scoped>
#section-content{

}
</style>



